@import url(https://fonts.googleapis.com/css?family=Kalam:400,700);
.post-it-note {
  width: 50%;
  min-width: 100px;
  margin: 1em auto;
  padding: 3em;
  background: #ffd707;
  position: relative;
  margin-bottom: 50px;
  text-align: left;
}

.post-it-note:after {
  content: "";
  position: absolute;
  bottom: -2em;
  left: 0;
  right: 2em;
  border-width: 2em;
  border-style: solid;
  border-color: #ffd707;
}

.post-it-note:before {
  content: "";
  position: absolute;
  bottom: -2em;
  right: 0;
  border-width: 2em 2em 0 0;
  border-style: solid;
  border-color: #d3b100 transparent;
}

.post-it-empty {
  text-align: center;
  font-size: 16px;
  border: 1px solid;
  padding: 50px;
  margin: 10px;

}

.post-it-close {
  cursor: pointer;
}

.board-content {
  display: flex;
  flex-direction: column;
}

.column {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-content: flex-start;
}

.column-content {
  min-width: 300px;
  max-width: 300px;
}

.column-title {
  text-align: center;
  font-size: 32px;
}

.column-form {
  margin-top: 10px;
  margin-left: 50px;
}

.column-input {
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 1px solid #000000;
}
body {
  font-family: 'Kalam', cursive;
}

.app-content {
  display: flex;
  flex-direction: row;
}
