.post-it-note {
  width: 50%;
  min-width: 100px;
  margin: 1em auto;
  padding: 3em;
  background: #ffd707;
  position: relative;
  margin-bottom: 50px;
  text-align: left;
}

.post-it-note:after {
  content: "";
  position: absolute;
  bottom: -2em;
  left: 0;
  right: 2em;
  border-width: 2em;
  border-style: solid;
  border-color: #ffd707;
}

.post-it-note:before {
  content: "";
  position: absolute;
  bottom: -2em;
  right: 0;
  border-width: 2em 2em 0 0;
  border-style: solid;
  border-color: #d3b100 transparent;
}

.post-it-empty {
  text-align: center;
  font-size: 16px;
  border: 1px solid;
  padding: 50px;
  margin: 10px;

}

.post-it-close {
  cursor: pointer;
}
