.board-content {
  display: flex;
  flex-direction: column;
}

.column {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-content: flex-start;
}

.column-content {
  min-width: 300px;
  max-width: 300px;
}

.column-title {
  text-align: center;
  font-size: 32px;
}

.column-form {
  margin-top: 10px;
  margin-left: 50px;
}

.column-input {
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 1px solid #000000;
}